import React, { useEffect, useState } from "react";
import { Link } from "gatsby";
import { Button, Card, Col, Container, Row } from "react-bootstrap";
import Loading from "../../components/common/loading";
import LoadMore from "../../components/common/loadmore";
// import { useQuery, gql } from "@apollo/client";

// const FILMS_QUERY = gql`
//   query MyQuery {
//     allDataJson(skip: 0, limit: 10) {
//       totalCount
//       pageInfo {
//         perPage
//         hasNextPage
//       }
//       nodes {
//         id
//         keyId
//       }
//     }
//   }
// `;

const BlogsData = (data) => {
  const { siteUrl, blogImages } = data;
  // console.log("data;;",data);
  // const { data: gqlData, loading, error } = useQuery(FILMS_QUERY);
  // console.log("gqlData;;", gqlData);

  let count = 0;
  const [blogsData, setBlogsData] = useState([]);
  // console.log("blogsData::", blogsData);
  // console.log("data::", data);
  // const [jsdata, setData] = useState([]);
  // const [isNextPage, setisNextPage] = useState(null);
  const [init, setInit] = useState(0);

  const { numberItemsPagination, loadMoreItems, isLoading } = LoadMore(6);
  // console.log("numberItemsPagination blog::", numberItemsPagination);

  const loadBlogs = async () => {
    const blogsArr = [];

    let propsData = data.blogs;
    propsData?.edges.sort((a, b) => b.node.keyId - a.node.keyId);
    // console.log("propsData:;", propsData);
    if (propsData?.edges?.length > 0) {
      for (let blog of propsData?.edges) {
        for (let blogImgObj of blogImages) {
          const jsonBlogImgEndPath = blog?.node?.image?.split("/");
          const graphBlogImgEndPath =
            blogImgObj?.node?.gatsbyImageData?.images?.fallback?.src?.split(
              "/"
            );
          if (jsonBlogImgEndPath?.[2] === graphBlogImgEndPath?.[4]) {
            blog.node.blogImg =
              blogImgObj?.node?.gatsbyImageData?.images?.fallback?.src;
            blogsArr.push({ blog });
            setBlogsData(blogsArr);
          }
        }
      }
    }
  };

  useEffect(() => {
    loadBlogs();
  }, [init]);
  blogsData.sort((a, b) => b.blog.blog_created_date - a.blog.blog_created_date);
  // console.log("propsData::", propsData);
  // console.log("blogImages::", blogImages);

  return (
    <Container fluid className="p-0">
      <div className="blog_details_bg">
        <h1>Trending Blogs</h1>
      </div>
      <div className="blog_list_bg">
        <Container className="blog_container_position">
          <Row className="blog_row_gap">
            {blogsData?.slice(0, numberItemsPagination).map((ele, ind) => {
              const { blog } = ele;
              let blogId = ind;
              if (blogId % 2 === 0) {
                count = count + 1;
              }
              return (
                <Col lg={6} xs={12} className="p-lg-0">
                  {count % 2 !== 0 ? (
                    <Card
                      key={ele.id}
                      className="jt_blog_card"
                      data-aos="fade-up"
                      data-aos-delay="400"
                    >
                      <Row className="mx-lg-0">
                        <Col
                          sm={6}
                          xs={12}
                          className="p-sm-0 d-flex justify-content-center "
                        >
                          <Link
                            to={`${blog?.node?.slug}`}
                            className="text-decoration-none  d-block "
                          >
                            <div
                              id="arrow_right"
                              className="jt_blog_img_wrapper"
                            >
                              <img
                                src={`${siteUrl}${blog?.node?.blogImg}`}
                                alt={blog?.node?.title}
                                className="jt_blog_img"
                              />
                              <div className="blog_arrow_first">
                                <svg
                                  xmlns="http://www.w3.org/2000/svg"
                                  width="15"
                                  height="18"
                                  viewBox="0 0 15 18"
                                  fill="none"
                                >
                                  <path
                                    d="M0 9L15 0.339745L15 17.6603L0 9Z"
                                    fill="white"
                                  />
                                </svg>
                              </div>
                            </div>
                          </Link>
                        </Col>
                        <Col
                          sm={6}
                          xs={12}
                          className="p-sm-0 d-flex justify-content-center "
                        >
                          <Link
                            to={`${blog?.node?.slug}`}
                            className="jt_blog_card_body"
                          >
                            <p className="jt_blog_date">
                              {blog?.node?.blog_created_date}
                            </p>

                            <p className="jt_blog_title">{blog?.node?.title}</p>
                            <p className="jt_blog_description">
                              {blog?.node?.short_description.slice(0, 65) +
                                "..."}
                            </p>
                          </Link>
                        </Col>
                      </Row>
                    </Card>
                  ) : (
                    <Card
                      key={ele.id}
                      className="jt_blog_card"
                      data-aos="fade-up"
                      data-aos-delay="400"
                    >
                      <Row className="mx-lg-0">
                        <Col
                          sm={6}
                          xs={12}
                          className="p-sm-0 d-flex justify-content-center order_two "
                        >
                          <Link
                            to={`${blog?.node?.slug}`}
                            className="jt_blog_card_body"
                          >
                            <p className="jt_blog_date">
                              {blog?.node?.blog_created_date}
                            </p>

                            <p className="jt_blog_title">{blog?.node?.title}</p>
                            <p className="jt_blog_description">
                              {blog?.node?.short_description.slice(0, 65) +
                                "..."}
                            </p>
                          </Link>
                        </Col>
                        <Col
                          sm={6}
                          xs={12}
                          className="p-0 d-flex justify-content-center order_one"
                        >
                          <Link
                            to={`${blog?.node?.slug}`}
                            className="text-decoration-none  d-block "
                          >
                            <div
                              id="arrow_right"
                              className="jt_blog_img_wrapper"
                            >
                              <img
                                src={`${siteUrl}${blog?.node?.blogImg}`}
                                alt={blog?.node?.title}
                                className="jt_blog_img"
                              />

                              <div className="blog_arrow_second">
                                <svg
                                  xmlns="http://www.w3.org/2000/svg"
                                  width="15"
                                  height="18"
                                  viewBox="0 0 15 18"
                                  fill="none"
                                >
                                  <path
                                    d="M15 9.22363L0 17.8839L0 0.563378L15 9.22363Z"
                                    fill="white"
                                  />
                                </svg>
                              </div>
                            </div>
                          </Link>
                        </Col>
                      </Row>
                    </Card>
                  )}
                </Col>
              );
            })}
            {/* <button
              disabled={
                isLoading || numberItemsPagination === blogsData?.length
              }
              className="blog_load_btn"
              onClick={() => loadMoreItems(blogsData?.length)}
            >
              Load more {isLoading && <Loading />}
            </button> */}
            {numberItemsPagination !== blogsData?.length && (
              <Button
                disabled={
                  isLoading || numberItemsPagination === blogsData?.length
                }
                className="blog_load_btn"
                onClick={() => loadMoreItems(numberItemsPagination + 2)}
              >
                Load more {isLoading && <Loading />}
              </Button>
            )}
          </Row>
        </Container>
      </div>
    </Container>
  );
};

export default BlogsData;
